@import "./src/styles/setup";

.song-preview {
  padding: rem(10px);
  transition: all 0.3s;
  border-radius: 6px;
  height: 50px;
  color: $light-grey;
  width: 100%;
  &:not(:last-child) {
    margin-bottom: 10px;
  }

  &:hover {
    background-color: $song-hover;
  }
  .add-song-btn {
    background-color: transparent;
    i {
      color: $light-green;
      transition: all 0.2s;
      :hover {
        color: $green;
      }
    }
  }
  .song-details {
    font-size: rem(16px);
    > * {
      margin-right: 16px;
    }
    transition: color 0.3s;
    cursor: pointer;

    img {
      width: rem(50px);

      object-fit: cover;
      border-radius: rem(5px);
    }
    .song-name {
      position: relative;
      display: block;
      text-transform: capitalize;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      max-width: rem(500px);
      @include respond(narrow) {
        max-width: rem(350px);
      }
      @include respond(min-mobile) {
        text-transform: capitalize;
        min-width: rem(200px);
        max-width: rem(104px);
        text-overflow: ellipsis;
      }
    }
    .song-duration {
      color: $light-grey-2;
      font-size: rem(12px);
      letter-spacing: 1.1px;
    }

    &:hover,
    &:hover > :first-child,
    &.played-song,
    &.played-song > :first-child {
      color: $green;
    }
  }

  .song-preview-icon {
    font-size: rem(15px);
    color: $white;
    transition: all 0.2s;

    &-remove {
      margin-left: rem(9px);
      margin-right: rem(3px);
    }
    cursor: pointer;

    &:hover {
      color: $green;
    }
  }
}
