@import "./src/styles/setup";
.user-to-edit {
  .img-user {
    display: block;
    border-radius: 50%;
    margin: 5px;
    border: 2px solid $mix-bgc;
    box-sizing: content-box;
    object-fit: cover;
    width: rem(120px);
    height: rem(120px);
    cursor: pointer;
  }
  .img-user-upload {
    cursor: pointer;
    font-size: rem(25px);
    color: $mix-bgc;
    margin: 5px;
    transition: all 0.2s;
    &:hover {
      color: $light-green;
    }
  }
  label {
    font-size: rem(20px);
    margin-bottom: rem(10px);
  }
  input {
    border-radius: 10px;
    border: 1px solid $light-grey;
    padding: 10px 10px;
    font-family: inherit;
    font-size: rem(18px);
    color: $player-bgc;
    background-color: $white;
    transition: all 0.3s;
    width: fit-content;
    overflow: hidden;

    &:focus {
      outline: none;
      color: $white;
      background-color: $player-top;
    }
  }
  .err-msg {
    color: #f44336f5;
  }
}
